/* stylelint-disable */

@mixin burger {
  --burger-width: 36px;
  --burger-height: 36px;
  --burger-line-height: 2px;

  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: calc(50% - 8px);
    width: 16px;
    height: var(--burger-line-height);
    background-color: currentColor;
    //border-radius: 100px;
    //transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    //transform: translateY(-50%, -50%);
  }

  &::before {
    top: calc(50% - 3px);
  }

  &::after {
    top: calc(50% + 3px);
  }

  //&__line {
  //  position: absolute;
  //  left: 0;
  //  top: 50%;
  //  width: 100%;
  //  height: var(--burger-line-height);
  //  background-color: currentColor;
  //  transform: translateY(-50%);
  //  transition: transform 0.3s ease-in-out;
  //}

  &--active {
    &::before {
      top: 50%;
      transform: rotate(45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    &::after {
      top: 50%;
      transform: rotate(-45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    .burger__line {
      transform: scale(0);
      transition: transform 0.3s ease-in-out;
    }
  }
}
